* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  cursor: url(data:image/png;base64,), auto;

}

ol,
ul {
  list-style: none;
}

.container {
  width: 70%;
  height: auto;
  margin: auto;
}